* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.list {
  padding: 0;
  margin: 0;
  list-style: none;
}

.country-text {
  font-size: 15px;
  font-weight: bold;
}

.span-text {
  font-size: 15px;
  font-weight: normal;
}

.flag {
  width: 40px;
  height: 20px;
}

.short-list {
  display: flex;
  vertical-align: middle;
}
